import { useState } from "react"
import { nGonState } from "./Polygon.types"
import { ngon, translate, Cubic, multiply, add } from "../../geometry";

export const useNgon = (sides: number, scale:number, rotation: number, cornerRadius: number, strokeWidth: number): [number[][], Cubic, number, React.Dispatch<React.SetStateAction<nGonState>>] => {
	const [state, setState] = useState<nGonState>({sides,scale,rotation,cornerRadius,strokeWidth});
	const radius = state.scale/2;
	const [cpts, vb] = ngon(state.sides, radius, state.rotation, state.cornerRadius, [0, 0]);
	const ssw = state.strokeWidth * scale;
	const hsw = ssw/2;
	const nvb = add(multiply(vb, -1), hsw);
	const points = translate(cpts, [nvb[0], nvb[1], nvb[0], nvb[1]]);
	const maxBound = multiply(add([vb[2], vb[3]], hsw), 2);
	const viewBox: Cubic = [0, 0, ...maxBound] as Cubic;
	return [points, viewBox, ssw, setState];
}